html {
	box-sizing: border-box;
}

* {
	box-sizing: inherit;
	margin: 0;
	padding: 0;
}

body {
	min-height: 100vh;
	background-repeat: no-repeat;
	background-size: cover;
	background: linear-gradient(
		45deg,
		#136383 0%,
		#1a9aba 50%,
		#158bab 51%,
		#40c7e6 100%
	);
}

.image-container {
	background-color: #02053b;
	padding: 50px 100px;
	flex: 1 0 70%;
	display: flex;
	align-items: center;
	justify-content: center;
}

.results {
	margin: 30px auto;
	width: 80%;
	height: 600px;
	display: flex;
}

